// ----------------------------------------------------------------------------------
// -- HexMap struct.
// ----------------------------------------------------------------------------------

var HexMap = function(width, height, rows, cols, hex, canvas_id)
{
    this.rows = rows;
    this.cols = cols;
    this.hex = hex;

    this.scroll_x = 0;
    this.scroll_y = 0;

    this.selected_virtual_hex = false;
    this.over_virtual_hex = false;
    this.cursor = [0, 0];

    this.canvas_id = canvas_id;
    this.map_screen = document.getElementById(canvas_id);
    this.ctx = this.map_screen.getContext('2d');

    this.map_screen.addEventListener('mousemove', this.e_mouse_move.bind(this));
    this.map_screen.addEventListener('click', this.e_mouse_click.bind(this));

    this.map_screen.width = width;
    this.map_screen.height = height;

    this.map_border_edges = [];
};

HexMap.prototype.screen_to_map = function(x, y)
{
    if (Object.prototype.toString.call(x) == '[object Array]') {
        y = x[1]; x = x[0];
    }

    return [x + this.scroll_x, y + this.scroll_y];
};

HexMap.prototype.map_to_screen = function(x, y)
{
    if (Object.prototype.toString.call(x) == '[object Array]') {
        y = x[1]; x = x[0];
    }

    return [x - this.scroll_x, y - this.scroll_y];
};

HexMap.prototype.valid_hex = function(virtual_hex) {
    return virtual_hex[0] >= 0 && virtual_hex[0] < this.rows && virtual_hex[1] >= 0 && virtual_hex[1] < this.cols;
};

HexMap.prototype.neighbours = function(real_hex)
{
    var nw_hex = this.hex.real_to_virtual(this.hex.goto(real_hex, 'nw'));
    var ne_hex = this.hex.real_to_virtual(this.hex.goto(real_hex, 'ne'));
    var e_hex = this.hex.real_to_virtual(this.hex.goto(real_hex, 'e'));
    var se_hex = this.hex.real_to_virtual(this.hex.goto(real_hex, 'se'));
    var sw_hex = this.hex.real_to_virtual(this.hex.goto(real_hex, 'sw'));
    var w_hex = this.hex.real_to_virtual(this.hex.goto(real_hex, 'w'));

    var neighbours = [
        this.valid_hex(se_hex),
        this.valid_hex(e_hex),
        this.valid_hex(ne_hex),
        this.valid_hex(nw_hex),
        this.valid_hex(w_hex),
        this.valid_hex(sw_hex)
    ];

    return neighbours;
};

HexMap.prototype.map_border_tiles = function()
{
    var border_list = [];

    for (var i = 0; i < this.cols; ++i) {
        border_list.push([0, i]);
    }

    for (var i = 0; i < this.rows; ++i) {
        border_list.push([i, this.cols - 1]);
    }

    for (var i = this.cols - 1; i >= 0; --i) {
        border_list.push([this.rows - 1, i]);
    }

    for (var i = this.rows - 1; i >= 0; --i) {
        border_list.push([0, i]);
    }

    return border_list;
};

HexMap.prototype.area_border_tiles = function(area)
{
    // for (var )
    // {}
};

HexMap.prototype.border = function(real_hex)
{
    var border = [];
    var neighbours = this.neighbours(real_hex);
    var end_added = false;

    for (var i = 0; i < 6; ++i)
    {
        if (!neighbours[i])
        {
            border.push(i);
            border.push((i + 1) % 6);
        }
    }

    return border;
};

HexMap.prototype.get_screen_point = function(event)
{
    var rect = this.map_screen.getBoundingClientRect();
    var point = [0, 0];

    point[0] = Math.floor(event.clientX - rect.left);
    point[1] = Math.floor(event.clientY - rect.top);

    return point;
};

HexMap.prototype.e_mouse_move = function(event)
{
    var point = this.screen_to_map(this.get_screen_point(event));
    this.over_virtual_hex = this.hex.real_to_virtual(this.hex.point_inside(point[0], point[1]));
};

HexMap.prototype.e_mouse_click = function(event)
{
    var point = this.screen_to_map(this.get_screen_point(event));
    this.selected_virtual_hex = this.hex.real_to_virtual(this.hex.point_inside(point[0], point[1]));
};

HexMap.prototype.draw_hex = function(real_hex, over)
{
    var center = this.hex.hex_to_pixel(real_hex);
    var points = this.hex.points(real_hex);

    over = over || false;

    this.ctx.save();
    this.ctx.strokeStyle = 'rgb(180, 180, 180)';
    this.ctx.fillStyle = 'rgb(255, 255, 255)';
    this.ctx.lineWidth = 2;

    if (over)
    {
        // ctx.strokeStyle = 'rgb(255, 0, 0)';
        // ctx.lineWidth = 1;
        this.ctx.fillStyle = 'rgb(240, 240, 240)';
    }

    this.ctx.beginPath();

    // -- Hex.

    for (var i = 0; i < points.length; ++i)
    {
        var p = this.map_to_screen(points[i]);

        p[0] -= 0.5;
        p[1] -= 0.5;

        if (i == 0)
            this.ctx.moveTo(p[0], p[1]);
        else
            this.ctx.lineTo(p[0], p[1]);
    }

    this.ctx.closePath();
    this.ctx.fill();
    this.ctx.stroke();

    // -- Labels.

    var center_screen = this.map_to_screen(center);
    var virtual_hex = this.hex.real_to_virtual(real_hex);
    var real_text = 'R: {' + real_hex[0] + ', ' + real_hex[1] + '}';
    var virtual_text = 'V: {' + virtual_hex[0] + ', ' + virtual_hex[1] + '}';

    this.ctx.font = "10px bold Tahoma";
    this.ctx.fillStyle = 'rgb(150, 150, 150)';
    this.ctx.fillText(real_text, center_screen[0] - 18, center_screen[1] - 3);
    this.ctx.fillText(virtual_text, center_screen[0] - 18, center_screen[1] + 10);

    this.ctx.restore();
};

HexMap.prototype.draw_map = function()
{
    this.ctx.fillStyle = '#fff';
    this.ctx.fillRect(0, 0, this.map_screen.width, this.map_screen.height);

    var show_over = false;
    this.map_border_edges.length = 0;

    for (var row = 0; row < this.rows; ++row)
    {
        for (var col = 0; col < this.cols; ++col)
        {
            if (this.over_virtual_hex && this.over_virtual_hex[0] == row && this.over_virtual_hex[1] == col)
                show_over = true;

            var real_hex = this.hex.virtual_to_real([row, col]);
            this.draw_hex(real_hex, false);
        }
    }

    if (show_over)
        this.draw_hex(this.hex.virtual_to_real(this.over_virtual_hex), true);
};

export default HexMap;