global.$ = global.jQuery = require('jquery');
require('bootstrap');

import Hex from 'hex';
import HexMap from 'hexmap';

// ----------------------------------------------------------------------------------
// -- HEXGRID.
// ----------------------------------------------------------------------------------

$(function()
{

    // ----------------------------------------------------------------------------------
    // -- Main.
    // ----------------------------------------------------------------------------------

    var hex = new Hex(40, 36, 40, 100, 50, false);
    var hex_map = new HexMap(800, 500, 4, 5, hex, 'hexgrid_screen');

    var render = function(timestamp)
    {
        hex_map.draw_map();
        window.requestAnimationFrame(render);
    };

    window.requestAnimationFrame(render);
});
