// ----------------------------------------------------------------------------------
// -- Helper routines.
// ----------------------------------------------------------------------------------

import HexMath from 'math.js';

var HexHelper =
{
    _hex_matrices: function(hex)
    {
        var a1 = [2 * hex.r[1], 0];
        var a2 = [hex.r[1], hex.r[0] + hex.r[2] * 0.5];

        if (hex.negative)
            a2[0] *= -1;

        hex.a1_len = HexMath.vec_len(a1);
        hex.a2_len = HexMath.vec_len(a2);

        hex.m[0][0] = a1[0];
        hex.m[1][0] = a1[1];

        hex.m[0][1] = a2[0];
        hex.m[1][1] = a2[1];

        hex.m[0][2] = hex.tx;
        hex.m[1][2] = hex.ty;

        hex.mi = HexMath.mat_inverse(hex.m);
    },

    _inside_polygon: function(points, x, y)
    {
        for (var i = 0; i < 6; ++i)
        {
            var current = points[i];
            var next = points[(i + 1) % 6];

            var v = (next[0] - current[0]) * (y - current[1]) - (next[1] - current[1]) * (x - current[0]);

            if (v > 0)
                return false;
        }

        return true;
    }
};

export default HexHelper;